/* 네비게이션 바 스타일 */
.navbar {
  display: flex; /* Flexbox 사용하여 가로 정렬 */
  align-items: center; /* 수직 가운데 정렬 */
  justify-content: space-between; /* 주변 여백에 따라 요소 사이의 여유 공간을 분산 */
  background: #273043; /* 배경색 변경 */
  color: #d4d4d4;
  padding: 20px 80px; /* 위 아래로 20px, 좌우로 80px의 안쪽 여백 설정 */
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.06); /* 그림자 추가 */
}

/* 내비게이션 바 메뉴 스타일 */
.navbar-menu {
  list-style: none; /* 리스트 스타일 제거 */
  padding: 0; /* 안쪽 여백 제거 */
  margin: 0; /* 바깥 여백 제거 */
  display: flex; /* Flexbox 사용하여 가로 정렬 */
  flex-direction: row; /* 세로 방향으로 배치 */
  color:#d4d4d4;
  background-color: #273043; /* 배경색 변경 */
  padding: 10px 30px; /* 상단과 하단의 패딩 변경 */
  
}

/* 내비게이션 바 아이템 스타일 */
.navbar-item {
  margin-right: 40px; /* 각 메뉴 사이 간격 설정 */
  position: relative;
}

.navbar-item:hover {
  color: #17cf97; /* 각 메뉴 사이 간격 설정 */
  position: relative;
}

/* 내비게이션 바 아이템 활성 상태 스타일 */
.navbar-item.active .submenu {
  display: block; /* 하위 메뉴 표시 */
}

/* 하위 메뉴 스타일 */
.submenu {
  list-style: none;
  padding: 0;
  margin: 0;
  display: none;
  position: absolute;
  background-color: #273043;
  padding: 40px 10px 5px 5px;
  left: 0;
  top: 100%;
  z-index: 1;
  white-space: nowrap; /* 줄 바꿈 방지 */
}

/* 하위 메뉴 아이템 스타일 */
.submenu li {
  margin-bottom: 5px; /* 아래쪽 여백 추가 */
}

/* 하위 메뉴 링크 스타일 */
.submenu li a {
  text-decoration: none; /* 링크 밑줄 제거 */
  color: #d4d4d4; /* 글자 색상 변경 */
}

/* 하위 메뉴 링크 호버 시 스타일 */
.submenu li a:hover {
  color: #17cf97; /* 호버 시 글자 색상 변경 */
}

/* 모바일 환경에서 네비게이션 바 스타일 변경*/
@media screen and (max-width: 300px) {
  .navbar {
    padding: 20px; /* 안쪽 여백 변경 */
  }

  /* 네비게이션 메뉴 스타일 */
  .navbar-menu {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: column; /* 모바일 화면에서 세로 방향으로 배치 */
    background-color: #273043;
    padding: 20px 0;
  }

  /* 네비게이션 메뉴 아이템 스타일 */
  .navbar-item {
    margin-bottom: 10px;
    position: relative; /* 하위 메뉴 위치 설정 */
  }

  /* 하위 메뉴 스타일 */
  .submenu {
    list-style: none;
    padding: 0;
    margin: 0;
    display: none; /* 초기에는 하위 메뉴 숨김 */
  }

  /* Mobile 화면에서 보이는 햄버거 아이콘 스타일 */
  .navbar-toggler {
    display: block; /* Mobile 화면에서만 보이도록 설정 */
    background: none;
    border: none;
    cursor: pointer;
}

  /* 햄버거 아이콘 스타일 */
  .navbar-toggler-icon {
    font-size: 24px;
    color: #d4d4d4;
  }

  .active .navbar-menu {
    display: flex; /* Flexbox 사용하여 가로 정렬 */
    flex-direction: under; /* 세로 방향으로 배치 */
  }

  .submenu {
    left: 100%; /* 왼쪽 여백 설정 */
    top: 0; /* 위쪽 여백 설정 */
    width: auto; /* 너비 자동 설정 */
    max-width: 200px; /* 최대 너비 설정 */
    white-space: nowrap; /* 줄 바꿈 방지 */
  }
}