/*테윌윈드 적용*/
@tailwind base;
@tailwind components;
@tailwind utilities;

@font-face {
  font-family: "GmarketSansMedium";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2001@1.1/GmarketSansMedium.woff")
    format("woff");
  font-weight: normal;
  font-style: normal;
}
/* index.css 파일 수정 */
html, body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  /* overflow: hidden; 필요한 경우 스크롤 숨기기 */
}

#root {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column; /* 컨텐츠를 수직으로 정렬 */
}

/* 지마켓산스 폰트 적용 */
html > body,
html > body input,
html > body select,
html > body textarea,
html > body pre,
html > body button {
  font-family: "GmarketSansMedium";
  text-underline-position: under;
  line-height: 1.3; /* 이 폰트 특성상 */
}