/* 로그인 페이지 전체 스타일 */
.login-page {
  background: url('https://images.unsplash.com/photo-1568605117036-5fe5e7bab0b7?q=80&w=1770&auto=format&fit=crop&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90oy1wYWdlfHx8fGVufDB8fHx8fA%3D%3D') no-repeat center center fixed;
  background-size: cover;
  height: 100vh; /* Ensure it covers the full viewport height */
  display: flex;
  justify-content: center;
  align-items: center;
}
.wrapper {
  width: 100%;
  max-width: 420px; /* 최대 너비 설정 */
  background:#fff; /* 배경색 변경: 흰색 */
  border: 2px solid rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(10px);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.1);
  color: #333; /* 폰트 색상 조정: 짙은 회색 */
  border-radius: 8px;
  padding: 30px 20px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.wrapper h1 {
  font-size: 28px; /* 폰트 크기 조정 */
  text-align: center;
  color: #333; /* 폰트 색상 조정 */
}

.wrapper .input-box {
  position: relative;
  width: 100%;
  height: 50px;
  margin: 10px 0; /* 입력 상자 간의 간격 조정 */
}

.input-box input {
  width: 100%;
  height: 100%;
  background: transparent;
  border: 1px solid #ccc;
  border-radius: 25px;
  font-size: 16px;
  color: #333;
  padding: 0 20px 0 45px; /* 왼쪽 패딩 조정 */
}

.input-box .icon {
  position: absolute;
  left: 15px;
  top: 50%;
  transform: translateY(-50%);
  font-size: 20px;
  color: #666;
}

.wrapper button {
  width: 100%;
  height: 45px;
  background-color: #0056b3; /* 기본 버튼 색상 조정 */
  border: none;
  border-radius: 22px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  cursor: pointer;
  font-size: 16px;
  color: #fff;
  font-weight: bold;
  margin-top: 20px;
}
.wrapper button:hover {
  background-color: #163658;
}
.login-form {
  display: flex; /* Flexbox를 사용하여 컨테이너 설정 */
  flex-direction: column; /* 세로 방향으로 자식 요소를 정렬 */
  align-items: center; /* 가로축을 기준으로 중앙 정렬 */
  width: 100%; /* 폼의 너비를 100%로 설정 */
}

.kakao-login-button {
  width: 90%; /* 버튼의 너비를 90%로 설정 */
  margin: 20px 0; /* 위, 아래 마진으로 버튼 주변 공간 추가 */
  padding: 10px 0; /* 내부 패딩 조정 */
  background-color: #f8e329!important; /* 배경색 설정 */
  color: #333!important;; /* 폰트 색상 */
  border-radius: 22px; /* 둥근 모서리 설정 */
  border: none; /* 테두리 제거 */
  display: flex; /* Flexbox 적용 */
  justify-content: center; /* 내부 요소 가운데 정렬 */
  align-items: center; /* 수직 방향 가운데 정렬 */
  cursor: pointer; /* 포인터 모양 변경 */
  font-size: 16px; /* 폰트 크기 설정 */
  text-align: center; /* 텍스트 가운데 정렬 */
}

.kakao-login-button .icon {
  margin-right: 8px; /* 아이콘과 텍스트 사이 간격 조정 */
}
.kakao-login-button:hover{
  background-color: #d8c516!important; /* 배경색 설정 */
}

.wrapper .register-link {
  font-size: 14px;
  text-align: center;
  margin-top: 20px;
}

.register-link p a {
  color: #0056b3;
  text-decoration: none;
  font-weight: bold;
}

.register-link p a:hover {
  text-decoration: none;
}

/* 반응형 디자인 추가 */
@media (max-width: 768px) {
  .wrapper {
    padding: 20px;
  }

  .input-box input {
    padding: 0 15px 0 40px; /* 모바일에서의 패딩 조정 */
  }

  .wrapper h1 {
    font-size: 24px;
  }
}