/* MyPage.css */
.page-container {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
  background-size: cover;
  filter: brightness(80%);
}

.heading {
  font-size: 2.5em;
  font-weight: bold;
  text-transform: uppercase;
  margin: 0.5em 0;
}

.paragraph {
  font-size: 1.2em;
  width: 60%;
  text-align: center;
  line-height: 1.5;
  margin-bottom: 2em;
}

.button {
  padding: 10px 20px;
  font-size: 1em;
  letter-spacing: 1px;
  color: #fff;
  background-color: #007BFF;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin: 0 10px;
  transition: background-color 0.3s ease;
}

.button:hover {
  background-color: #0056b3;
}

.button-container {
  display: flex;
  justify-content: center;
}
